class QueryBuilder {
  siteUrl = ''
  query = '?'
  constructor(siteUrl) {
    if(siteUrl !== '') {
      this.siteUrl = siteUrl
    } else {
      this.siteUrl = ''
    }
  }

  set = (field, value) => {
    if(this.query.indexOf('=') > -1) {
      this.query += '&'
    }
    this.query += `${field}=${value}`
  }

  get = () => {
    return this.siteUrl + this.query; 
  }
}

export default QueryBuilder