import React from "react"
import Sidebar from "react-sidebar"
import AssignedList from "./AssignedList"
import StudentsList from "./StudentsList"
import { ContextLayout } from "../../../../utility/context/Layout"
import "../../../../assets/scss/pages/app-email.scss"
import { getInitialData as getLevels } from "../../../../redux/actions/level";
import { getParentData as getCategories } from "../../../../redux/actions/category";
import { getDataByType as getTopics } from "../../../../redux/actions/topic";
import { connect } from "react-redux"
import QueryString from "query-string"

const mql = window.matchMedia(`(min-width: 992px)`);

class AssignAndMarkIndex extends React.Component {
  pageType;
  state = {
    composeMailStatus: false,
    sidebarDocked: mql.matches,
    sidebarOpen: false,
    filterOption: {
      isMCQ: true,
      rangePicker: '',
      selectedCategoryId: '',
      selectedLevelId: '',
      selectedTopicId: '',
    },
    currentStudent: null 
  }

  constructor(props) {
    super(props);
    this.pageType = props.pageType
  }

  UNSAFE_componentWillMount() {
    mql.addListener(this.mediaQueryChanged)
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    this.pageType = nextProps.pageType
  }

  componentDidMount() {
    this.props.getLevels();
    this.props.getCategories();
    this.props.getTopics('ASSESSMENT');

    var query = QueryString.parse(this.props.location.search)

    this.pageType = query.pageType;
  }

  componentWillUnmount() {
    mql.removeListener(this.mediaQueryChanged)
  }

  handleComposeSidebar = status => {
    if (status === "open") {
      this.setState({
        composeMailStatus: true
      })
    } else {
      this.setState({
        composeMailStatus: false
      })
    }
  }

  onSetSidebarOpen = open => {
    this.setState({ sidebarOpen: open })
  }

  mediaQueryChanged = () => {
    this.setState({ sidebarDocked: mql.matches, sidebarOpen: false })
  }

  handleMainAndComposeSidebar = () => {
    this.handleComposeSidebar("close")
    this.onSetSidebarOpen(false)
  }

  handleSelectStudent = (currentStudent) => {
    if(!Object.is(currentStudent, this.state.currentStudent)) {
      this.setState({
        currentStudent
      })
    }
  }

  render() {
    const {
      currentStudent
    } = this.state

    return (
      <React.Fragment>
        <div className="email-application position-relative">
          <div
            className={`app-content-overlay ${
              this.state.composeMailStatus || this.state.sidebarOpen ? "show" : ""
            }`}
            onClick={this.handleMainAndComposeSidebar}
          />
          <ContextLayout.Consumer>
            {context => (
              <Sidebar
                sidebar={
                  <StudentsList
                    handleComposeSidebar={this.handleComposeSidebar}
                    mainSidebar={this.onSetSidebarOpen}
                    routerProps={this.props}
                    handleSelectStudent={(stuent) => this.handleSelectStudent(stuent)}
                  />
                }
                docked={this.state.sidebarDocked}
                open={this.state.sidebarOpen}
                sidebarClassName="sidebar-content email-app-sidebar d-flex h-100 -0"
                touch={false}
                contentClassName="sidebar-children"
                pullRight={context.state.direction === "rtl"}>
                ""
              </Sidebar>
            )}
          </ContextLayout.Consumer>
          <AssignedList
            mainSidebar={this.onSetSidebarOpen}
            routerProps={this.props}
            currentStudent={currentStudent}
            pageType={this.pageType}
          />
        </div>
      </React.Fragment>
    )
  }
}


const mapStateToProps = (state) => {
  return {
    levels: state.level.allData,
    categories: state.category.allData,
    topics: state.topic.dataPerType
  }
}

const mapDispatchToProps = {
  getLevels,
  getCategories,
  getTopics,
}

export default connect(mapStateToProps, mapDispatchToProps)(AssignAndMarkIndex)