import * as Types from "../types";
import { Axios } from "../root"
import { displayServerErrorMessage, displaySuccessMessage, displayInfo } from '../../../service/Alert';


export const getData = (params) => {
  return async (dispatch, getState) => {
    // let userRole = getState().auth.userRole;
    await Axios.get(`/management/topics?page=${params.page}&perPage=${params.perPage}`).then(response => {
      dispatch({
        type: Types.FETCH_TOPICS,
        data: response.data.topics,
        totalPages: response.data.totalPages,
        params
      })
    })
  }
}

export const getMangementInitialData = () => {
  return async (dispatch, getState) => {
    await Axios.get(`/management/topics`).then(response => {
      dispatch({ type: Types.FETCH_ALL_TOPIC, data: response.data.topics })
    })
  }
}
export const getInitialData = () => {
  return async (dispatch, getState) => {
    await Axios.get(`/management/topics`).then(response => {
      dispatch({ type: Types.FETCH_ALL_TOPIC, data: response.data.topics })
    })
  }
}

export const addData = (obj) => {
  return (dispatch, getState) => {
    let params = getState().topic.params;
    Axios
      .post("/topic/create", obj)
      .then(response => {
        displaySuccessMessage("Successfully Submitted!")
        dispatch(getData(params))
      })
      .catch(displayServerErrorMessage)
  }
}

export const filterData = value => {
  return dispatch => dispatch({ type: Types.FILTER_TOPIC , value })
}

export const deleteData = obj => {
  return dispatch => {
    
    Axios
      .delete("/topic/" + obj._id)
      .then(response => {
        displaySuccessMessage(`${obj.name} topic have been deleted!`)
        dispatch({ type: Types.DELETE_TOPIC, obj })
      })
      .catch(displayServerErrorMessage)
  }
}

export const deleteDatas = ids => {
  return (dispatch, getState) => {
    let params = getState().topic.params;
    Axios
      .put("/delete-topics", {ids: ids})
      .then(response => {
        if(response.data.deletedIds.length > 0) {
          displaySuccessMessage(`${response.data.deletedIds.length} topic have been deleted!`)
        }
        if(response.data.usedIds.length > 0) {
          displayInfo(`${response.data.usedIds.length} topics have relevant Lesson or Assessment!`)
        }
        if(response.data.deletedIds.length > 0) {
          dispatch(getData(params))
          dispatch({ type: Types.DELETE_TOPICS, ids: response.data.deletedIds })
        }
      })
      .catch(displayServerErrorMessage)
  }
}

export const updateData = obj => {
  return (dispatch, getState) => {
    
    let params = getState().topic.params;

    console.log("obj", obj);    
    Axios
      .put("/topic/" + obj._id, obj)
      .then(response => {
        dispatch({ type: Types.UPDATE_TOPIC, obj })
        dispatch(getData(params))
      })
      .catch(displayServerErrorMessage)
  }
}

export const getDataByType = (type) => {
  // type | LESSON | ASSESSMENT
  return async (dispatch) => {
    await Axios.get(`/topics?type=${type}`).then(response => {
      dispatch({
        type: Types.FETCH_TOPICS_BY_TYPE,
        data: response.data.topics,
      })
    })
  }
}