import React from "react"
import { ListGroup, ListGroupItem } from "reactstrap"
import PerfectScrollbar from "react-perfect-scrollbar"
import { X } from "react-feather"
import { changeFilter } from "../../../../redux/actions/email/index"
import { connect } from "react-redux"
import { getMyStudents } from '../../../../redux/actions/user'

import "react-draft-wysiwyg/dist/react-draft-wysiwyg.css"
import "../../../../assets/scss/plugins/extensions/editor.scss"
import  '../../../../assets/scss/custompages/assign.scss';

class StudentsListSideBar extends React.Component {
  static getDerivedStateFromProps(nextProps, state) {
    if(!Object.is(nextProps.users.mystudents, state.students)) {
      return {
        students: nextProps.users.mystudents
      }
    }

    return null;
  }
  
  state = {
    modal: false,
    activedStudentId: 0,
  }
  
  componentDidMount() {
    this.props.getMyStudents();
  }

  handleSelectStudent = (student) => {
    this.setState({
      activedStudentId: student._id
    });
    this.props.handleSelectStudent(student);
  }


  render() {

    const { activedStudentId, students } = this.state;

    return (
      <React.Fragment>
        <div
          className="sidebar-close-icon"
          onClick={() => this.props.mainSidebar(false)}
        >
          <X size={18} />
        </div>
        <div className="email-app-menu students-list">
          
          <PerfectScrollbar
            className="student-menu-list"
            options={{
              wheelPropagation: false
            }}
          >
            <div className="text-center" style={{marginBottom: 13}}>
              <h3 className='p-1'>Students</h3>
            </div>
            <ListGroup className="font-medium-1 students-group">
              {
                students.map((student, index) => 
                  <ListGroupItem
                    onClick={() => {
                      this.handleSelectStudent(student)
                    }}
                    key={index}
                    active={activedStudentId === student._id}
                    className="cursor-pointer"
                  >
                    <span className="avatar avatar-md m-0">
                      <img
                        src={student.avatar}
                        alt={student.name}
                        height="38"
                        width="38"
                      />
                    </span>
                    <span className="align-middle ml-1 text-bold-600">{(student.firstname + " " + student.lastname).substr(0, 10)}</span>
                  </ListGroupItem>
                )
              }
            </ListGroup>
          </PerfectScrollbar>
        </div>
      </React.Fragment>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    users: state.users
  }
}

const mapDispatchToProps = {
  getMyStudents,
  changeFilter
}

export default connect(mapStateToProps, mapDispatchToProps)(StudentsListSideBar)
