import QueryBuilder from '../classess/QueryBuilder'

class AssignedFilter {
  _id // id 
  title // string
  worksheet // worksheetid
  assignedTo // students id
  createdBy // user id
  startTime // period type is date
  endTime // date

  constructor(params) {
    this._id        = params._id
    this.title      = params.title
    this.worksheet = params.worksheet
    this.assignedTo = params.assignedTo
    this.createdBy  = params.createdBy
    this.startTime  = params.startTime
    this.endTime  = params.endTime
  }

  buildQuery = () => {
    let query = new QueryBuilder('')
    if(this._id) {
      query.set('_id', this._id);
    }
    if(this.title) {
      query.set('title', this.title);
    }
    if(this.worksheet) {
      query.set('worksheet', this.worksheet);
    }
    if(this.assignedTo) {
      query.set('assignedTo', this.assignedTo);
    }
    if(this.createdBy) {
      query.set('createdBy', this.createdBy);
    }
    if(this.startTime) {
      query.set('startTime', this.startTime);
    }
    if(this.endTime) {
      query.set('endTime', this.endTime);
    }

    var queryStr = query.get();
    return queryStr;
  }
}

export default AssignedFilter;