import { history } from "../../../history";
import * as Types from "../types";
import {Axios} from "../root"
import { subRooter } from "./../../../configs/vars"


export const getTutors = user => {
  return dispatch => {
    Axios
      .get("/tutors")
      .then(response => {
        if (response.data) {
          var tutors = response.data.tutors;
          dispatch({
            type: Types.FETCH_TEACHERS,
            payload: tutors
          })
        }
      })
      .catch(err => console.log(err))
  }
}

export const getMyStudents = () => {
  return dispatch => {
    Axios
      .get("/tutor/users")
      .then(response => {
        if (response.data) {
          var users = response.data.users;
          dispatch({
            type: Types.FETCH_MY_STUDENTS,
            payload: users
          })
        }
      })
      .catch(err => console.log(err))
  }
}

export const changeFilter = filter => {
  return dispatch => {
    dispatch({ type: "CHANGE_FILTER", filter })
    history.push(`${subRooter}/assign/${filter}`)
    // dispatch(getTodos({ filter }))
  }
}