import { Axios } from "../root"
import { displayServerErrorMessage, displaySuccessMessage } from '../../../service/Alert';

export const textMark = (assign_id, answer_id, mark, questions) => {
    return async dispatch => {
        let data = {assign_id, mark, answer_id, questions};
        Axios
        .put(`/tutor/mark/text`, {data})
        .then(response => {
            displaySuccessMessage("Successfully changed!");
        })
        .catch(displayServerErrorMessage)
    }
}

