import React, { Component } from 'react'
import PropTypes from 'prop-types'
import {
  Media,
} from "reactstrap"
import {
  FileText,
  Trash
} from "react-feather"
import { matchYMD } from "../../../../../service/helper";

class AssignItem extends Component {
  static propTypes = {
    key: PropTypes.string,
    assignedTask: PropTypes.object,
    handleAssignDetails: PropTypes.func,
    handleDeleteAssign: PropTypes.func,
  }

  render() {
    const { assignedTask, key } = this.props

    return (
      <Media
        tag="li"
        key={key}
        className="mail-unread"
      >
        <Media className="pr-50 text-primary" tag="div" left>
          <FileText size={40} />
        </Media>
        <Media body>
          <div className="user-details flex-wrap m-0">
            <div className="mail-items"
              onClick={() => {
                this.props.handleAssignDetails("open", assignedTask)
              }}
            >
              <h5 className="text-bold-600 mb-25 ">{assignedTask.worksheet.name}</h5>
              <p style={{maxHeight: "20px"}} className="text-truncate m-0">{assignedTask.title}</p>
            </div>
            <div className="mail-meta">
              <div className="float-right">
                <div>
                  <span>
                    Assigned At: {matchYMD(assignedTask.createdAt) }
                  </span>
                  <p className="m-0">Topic: { assignedTask.worksheet.name } </p>
                </div>
                <div className="text-right">
                  <Trash size={20} className="danger"
                    onClick={() => this.props.handleDeleteAssign(assignedTask)}
                  />
                </div>
              </div>
            </div>
          </div>
        </Media>
      </Media>
    )
  }
}
export default AssignItem