import React from "react"
import {
  ArrowLeft,
  Check,
  ChevronDown,
  Home,
  Mic,
  Book,
  MessageSquare
} from "react-feather"
import {
  Row,
  Col,
  Card,
  CardHeader,
  CardBody,
  Media,
  Input,
  Collapse,
  CardTitle,
  Button
} from "reactstrap"
import PerfectScrollbar from "react-perfect-scrollbar"
import ReactAudioPlayer from 'react-audio-player'
import Checkbox from "../../../../components/@vuexy/checkbox/CheckboxesVuexy"
import { matchYMD } from "../../../../service/helper";
import { Component } from "react"
import { textMark, voiceMark } from "../../../../redux/actions/mark"
import { connect } from "react-redux"

class MCQAnswerComponent  extends React.Component {
  render () {
    let question  = this.props.question;
    
    return <div className="collapse-margin" key={question._id}>
      <Card>
        <CardHeader>
          <CardTitle className="lead collapse-title collapsed w-100">
            <Media>
              <Media body className='d-flex' tag="div">
                <div className="d-inline-block mr-1 mt-auto mb-auto">
                  <Checkbox
                    color="success"
                    icon={
                      <Check className="vx-icon" size={16} />
                    }
                    defaultChecked={true}
                    title='Completed'
                  />
                </div>
                <div className='d-flex mt-auto mb-auto'  
                  onClick={() => this.props.toggle(question._id)}
                >
                  <h5 className='mt-auto mb-auto'> Question 16 - </h5>
                  <h5 className='mt-auto mb-auto'>MCQ Answers</h5>
                </div>
              </Media>
              <ChevronDown size={15} className="collapse-icon m-auto" />
            </Media>
          </CardTitle>
        </CardHeader>
        <Collapse
          isOpen={question._id === this.props.collapseID}
          onEntering={() => this.props.onEntering(question._id)}
          onEntered={() => this.props.onEntered(question._id)}
          onExiting={() => this.props.onExiting(question._id)}
          onExited={() => this.props.onExited(question._id)}
        >
          <CardBody className="pl-3">
            {
              !question.answer && <p>Not answered yet </p>
            }
            <ul className="list-unstyled">
              {
                question.MCQs && question.MCQs.map((mcq) => (
                  <li key={mcq._id}>
                    {
                      question.answer && question.answer.selectedIndex ===  mcq.index ? 
                      <Row>
                        <Checkbox
                          color="success"
                          icon={
                            <Check className="vx-icon" size={16} />
                          }
                          disabled
                          defaultChecked={question.answer.selectedIndex === mcq.index}
                          title='Answered'
                        />
                        <p className="pl-1">{mcq.text}</p>
                        {
                          mcq.isCorrect ? <p className="pl-1">Correct answer</p> : <p className="pl-1">Wrong answer</p>
                        }
                      </Row>
                      :
                      <Row>
                        <Checkbox
                          color="primary"
                          icon={
                            <Check className="vx-icon" size={16} />
                          }
                          disabled
                          title='Answered'
                        />
                        <p  className="pl-1">{mcq.text}</p>
                      </Row>
                      
                    }
                  </li>
                ))
              }
            </ul>
          </CardBody>
        </Collapse>
      </Card>
    </div>
  }
}

class TextAnswerComponent extends Component {
  state = {
    mark: 0,
  }

  handleSubmitMark = (id) => {
    // api call
  }

  render() {
    let question = this.props.question
    return <div className="collapse-margin" key={question._id}>
          <Card>
            <CardHeader>
              <CardTitle className="lead collapse-title collapsed w-100">
                <Media>
                  <Media body className='d-flex' tag="div">
                    <div className="d-inline-block mr-1 mt-auto mb-auto">
                      <Checkbox
                        color="success"
                        icon={
                          <Check className="vx-icon" size={16} />
                        }
                        defaultChecked={true}
                        title='Completed'
                      />
                    </div>
                    <div className='d-flex mt-auto mb-auto' 
                      onClick={() => this.props.toggle(question._id)}
                    >
                      <h5 className='mt-auto mb-auto'> Question 16 - </h5>
                      <h5 className='mt-auto mb-auto'>Text Answers</h5>
                    </div>
                    <div className="position-relative has-icon-left w-25 pl-1 ml-auto mr-1">
                      <Input type="number" 
                        maxLength={2}
                        onChange={(e) => {
                          this.setState({
                            mark: e.target.value
                          })
                        }} 
                        round={'true'}
                        placeholder={this.props.mark} 
                      />
                      <div className="form-control-position">
                        <Book size={15} />
                      </div>
                    </div>
                    
                    <div>
                      <Button 
                        onClick={textMark(this.props.assingId, this.props.answerId, this.state.mark, this.props.questions)}
                      > 
                      Submit
                        <MessageSquare size={12}/>
                      </Button>
                    </div>
                  </Media>
                  <ChevronDown size={15} className="collapse-icon m-auto" />
                </Media>
              </CardTitle>
            </CardHeader>
            <Collapse
              isOpen={question._id === this.props.collapseID}
              onEntering={() => this.props.onEntering(question._id)}
              onEntered={() => this.props.onEntered(question._id)}
              onExiting={() => this.props.onExiting(question._id)}
              onExited={() => this.props.onExited(question._id)}
            >
              <CardBody>
                { question && question.answer.textAnswer }
              </CardBody>
            </Collapse>
          </Card>
        </div>
  }
}

class VoiceAnswerComponent extends Component {
  state = {
    mark: 0,
  }
  
  render() {
    let question = this.props.question
    return <div className="collapse-margin" key={question._id}>
    <Card>
      <CardHeader>
        <CardTitle className="lead collapse-title collapsed w-100">
          <Media>
            <Media body className='d-flex' tag="div">
              <div className="d-inline-block mr-1 mt-auto mb-auto">
                <Checkbox
                  color="success"
                  icon={
                    <Check className="vx-icon" size={16} />
                  }
                  defaultChecked={true}
                  title='Completed'
                />
              </div>
              <div className='d-flex mt-auto mb-auto' 
                onClick={() => this.props.toggle(question._id)}
              >
                <h5 className='mt-auto mb-auto'> Question 16 - </h5>
                  <Mic className='mt-auto mb-auto' size={16} color='goldenrod' />
                  <h5 className='mt-auto mb-auto'>Voice Recording</h5>
              </div>
              <div className="position-relative has-icon-left w-25 pl-1 ml-auto mr-1">
                <Input type="number" 
                  maxLength={2}
                  onChange={(e) => {
                    this.setState({mark : e.target.value})
                  }} 
                  round={'true'}
                  placeholder={this.props.mark} 
                />
                <div className="form-control-position">
                  <Book size={15} />
                </div>
              </div>
              <div>
                <Button 
                  onClick={() => this.props.voiceMark(this.props.assingId, this.props.answerId, this.state.mark, this.props.questions)}
                > 
                Submit
                  <MessageSquare size={12}/>
                </Button>
              </div>
            </Media>
            <ChevronDown size={15} className="collapse-icon m-auto" />
          </Media>
        </CardTitle>
      </CardHeader>
      <Collapse
        isOpen={question._id === this.props.collapseID}
        onEntering={() => this.props.onEntering(question._id)}
        onEntered={() => this.props.onEntered(question._id)}
        onExiting={() => this.props.onExiting(question._id)}
        onExited={() => this.props.onExited(question._id)}
      >
        <CardBody>
          <ReactAudioPlayer src={question.answer && question.answer.voiceUrl} controls />
        </CardBody>
      </Collapse>
    </Card>
  </div>
  }
}

const VoiceAnswer = connect(null, {
  voiceMark
})(VoiceAnswerComponent)

class MarkingComponent extends React.Component {
  state = {
    showReplies: false,
    activeTab: "1",
    collapseID: "",
    status: "Closed"
  }
  //  collapse start

  toggleTab = tab => {
    if (this.state.activeTab !== tab) {
      this.setState({ activeTab: tab })
    }
  }

  toggleCollapse = collapseID => {
    this.setState(prevState => ({
      collapseID: prevState.collapseID !== collapseID ? collapseID : ""
    }))
  }

  onEntered = id => {
    if (id === this.state.collapseID) this.setState({ status: "Opened" })
  }
  onEntering = id => {
    if (id === this.state.collapseID) this.setState({ status: "Opening..." })
  }

  onExited = id => {
    if (id === this.state.collapseID) this.setState({ status: "Closed" })
  }

  onExiting = id => {
    if (id === this.state.collapseID) this.setState({ status: "Closing..." })
  }
  // collapse end

  handleReplies = bool => {
    this.setState({
      showReplies: bool
    })
  }

  render() {
    const { currentAssign } = this.props;

    let questions = [];
    if(currentAssign) {
      questions = currentAssign.worksheet.assessments;

      questions = questions.map((item) => {
        let answer = currentAssign.answers.find((answer) => answer.assessment._id === item._id);
  
        item.answer = answer;
        return item  
      });
    }

    const accordionMarginItems = questions.map((question, index) => {
      if(question.isMCQ) {
        return <MCQAnswerComponent key={question._id}
          question={question} toggle={(id) => this.toggleCollapse(id)}
          assingId={currentAssign._id}
          collapseID={this.state.collapseID}
          onEntered={(id) => this.onEntered(id)}
          onEntering={(id) => this.onEntering(id)}
          onExited={(id) => this.onExited(id)}
          onExiting={(id) => this.onExiting(id)}
        />
      } else if(!question.answer) {
        return <div className="collapse-margin" key={question._id}>
          <Card>
            <CardHeader>
              <CardTitle className="lead collapse-title collapsed w-100">
                <Media>
                  <Media body className='d-flex' tag="div">
                    <div className="d-inline-block mr-1 mt-auto mb-auto">
                      <Checkbox
                        color="success"
                        icon={
                          <Check className="vx-icon" size={16} />
                        }
                        defaultChecked={false}
                        title='Completed'
                        readOnly
                      />
                    </div>
                    <div className='d-flex mt-auto mb-auto' 
                      onClick={() => this.toggleCollapse(question._id)}
                    >
                      <h5 className='mt-auto mb-auto'> Question 16 - </h5>
                    </div>
                    <div className="position-relative has-icon-left w-25 pl-1 ml-auto mr-1">
                      <p>This question is not answered yet</p>
                    </div>
                  </Media>
                  <ChevronDown size={15} className="collapse-icon m-auto" />
                </Media>
              </CardTitle>
            </CardHeader>
          </Card>
        </div>
      } else if(question.answer.voiceUrl) {
        return <VoiceAnswer key={question._id} question={questions} toggle={(id) => this.toggleCollapse(id)}  collapseID={this.state.collapseID} 
          onEntered={(id) => this.onEntered(id)}
          onEntering={(id) => this.onEntering(id)}
          onExited={(id) => this.onExited(id)}
          onExiting={(id) => this.onExiting(id)}
          assingId={currentAssign._id}
          mark={question.answer.mark}
          answerId={question.answer._id}
          questions={questions}
        />
      } else {
        return <TextAnswerComponent key={question._id} question={question} toggle={(id) => this.toggleCollapse(id)} collapseID={this.state.collapseID} 
          onEntered={(id) => this.onEntered(id)}
          onEntering={(id) => this.onEntering(id)}
          onExited={(id) => this.onExited(id)}
          onExiting={(id) => this.onExiting(id)}
          mark={question.answer.mark}
          assingId={currentAssign._id}
          answerId={question.answer._id}
          questions={questions}
        />
      }
    })

    return (
      <div
        className={`email-app-details ${
          this.props.currentStatus ? "show" : ""
        }`}
      >
        <div className="email-detail-header">
          <div className="email-header-left d-flex align-items-center mb-1">
            <ArrowLeft
              size={20}
              className="mr-1 cursor-pointer"
              onClick={() => {
                this.props.handleAssignDetails("close")
                this.handleReplies(false)
              }}
            />
            <h4 className="mb-0">{currentAssign && currentAssign.title}</h4>
          </div>
          <div className="email-header-right mb-1 ml-2 pl-1">
            <ul className="list-inline m-0">
              <li
                className="list-inline-item mr-1 mail-unread"
                onClick={() => {
                  this.props.handleAssignDetails("close")
                }}
              >
                <span className="action-icon">
                  <Home size={22} />
                </span>
              </li>
            </ul>
          </div>
        </div>
        <PerfectScrollbar
          className="email-scroll-area"
          options={{
            wheelPropagation: false
          }}
        >
          {
            currentAssign ? <Row>
              <Col sm="12">
                <Card className="px-1 mt-1">
                  <CardHeader className="email-detail-head ml-75 border-0">
                    <div className="user-details justify-content-between align-items-center flex-wrap">
                      <div className="mail-items">
                        <h4>Worksheet Name: {currentAssign.worksheet.name}</h4>
                        <h4 className="mb-0">Title: {currentAssign.title}</h4>
                      </div>
                    </div>
                    <div className="mail-meta-item ml-auto">
                      <div className="mail-date mb-1">
                        <span>Assigned At: {matchYMD(currentAssign.createdAt)}</span>
                      </div>
                      <div className="mail-time mb-1">
                        <span>Update At: {matchYMD(currentAssign.updatedAt)}</span>  
                      </div>
                    </div>
                  </CardHeader>
                </Card>
                <div className="vx-collapse">{accordionMarginItems}</div>
              </Col>
            </Row>
          : <div></div>
          }
        </PerfectScrollbar>
      </div>
    )
  }
}

export default MarkingComponent
