import React from "react"
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb"
import Configment from "./sub_configment"

class AssignPage extends React.Component {

  render() {
    return (
      <React.Fragment>
        <Breadcrumbs
          breadCrumbTitle="Assign"
          breadCrumbParent="Areas"
          breadCrumbActive="Assign"
        />
        <Configment pageType="assign" {...this.props} />
      </React.Fragment>
    )
  }
}

export default AssignPage
