import * as Types from "../types";
import { Axios } from "../root"
import { displaySuccessMessage, displayServerErrorMessage, displayErrorMessage } from '../../../service/Alert';
import Filter from "./assignedFilterClass";

export const addData = ({ title, description, worksheet, assignedTo }) => {
  return  dispatch => {
    Axios
      .post("/assign/create", {
        title, description, worksheet, assignedTo
      })
      .then(response => {
        dispatch({ type: Types.ADD_ASSIGN, obj: response.data.assign })
        displaySuccessMessage('Successfully assigned!')
      })
      .catch(displayServerErrorMessage)
  }
}

export const updateData = ({title, description, worksheet, _id}) => {
  return  dispatch => {
    Axios
      .put(`/assign/update/${_id}`, {
        title, description, worksheet
      })
        .then(response => {
          dispatch({ type: Types.UPDATE_ASSIGN, obj: response.data.assign })
          displaySuccessMessage('Successfully updated!')
        })
        .catch(displayServerErrorMessage)
  }
}


export const getAssignedListByFilter = ({
  _id,
  title,
  worksheet,
  assignedTo,
  createdBy,
  startTime,
  endTime,
}) => {
  return (dispatch) => {
    let filter = new Filter({
      _id:        _id,
      title:      title,
      worksheet:  worksheet,
      assignedTo: assignedTo,
      createdBy:  createdBy,
      startTime:  startTime,
      endTime:    endTime,
    });
    let query = filter.buildQuery();

    Axios
      .get(`/assigns/filter${query}`)
      .then(response => {
        dispatch({ type: Types.FETCH_ASSIGNS_BY_FILTER, data: response.data.assignedList})
      })
      .catch(displayServerErrorMessage)
  }
}

export const deleteData = (assign) => {
  return dispatch => {
    Axios
      .delete(`/assign/` + assign._id)
      .then((response) => {
        if(response.data.success) {
          displaySuccessMessage(`Successfully deleted ${assign.title}!`)
          dispatch({ type: Types.DELETE_ASSIGN, obj: assign});
        } else {
          displayErrorMessage("something went wrong!")
        }
      })
      .catch(displayServerErrorMessage)
  }
}