import React from "react"
import {
  ArrowLeft,
  Home,
} from "react-feather"
import {
  Row,
  Col,
  Card,
  Input,
  FormGroup,
  Label,
  Button,
  FormFeedback,
} from "reactstrap"
import PerfectScrollbar from "react-perfect-scrollbar"
import { connect } from 'react-redux'
import Select from 'react-select'
import "../../../../assets/scss/plugins/forms/react-select/_react-select.scss"

import {
  getInitialData as getWorkSheets,
} from "../../../../redux/actions/worksheet"
import {
  addData as addAssign,
  updateData as updateAssign
} from "../../../../redux/actions/assigned"

class WorkSheetsList extends React.Component {
  static getDerivedStateFromProps(nextProps, state) {
    if(!Object.is(nextProps.currentStudent, state.currentStudent)) {
      return {
        currentStudent: nextProps.currentStudent
      }
    }

    if(!Object.is(nextProps.currentAssign, state.currentAssign)) {
      let currentAssign  = nextProps.currentAssign;

      if(currentAssign) {
        return {
          title:              currentAssign.title,
          description:        currentAssign.description,
          selectedWorkSheet:  currentAssign.worksheet._id,
          currentAssign:      currentAssign
        }
      } else {
        return {
          title:              "",
          description:        "",
          selectedWorkSheet:  "",
          currentAssign:      null
        }
      }
    }
    return null;
  }
  state = {
    title:                "", // strign
    description:          "", // string
    currentStudent:       null, // object
    selectedWorkSheet:    null, // id of worksheet
    currentAssign:        null // object of assign.
  }

  componentDidMount() {
    this.props.getWorkSheets();
  }

  componentDidUpdate(prevProps, prevState) {
    if(!Object.is(this.props.currentStudent, prevProps.currentStudent)) {
      this.props.handleAssignDetails("close")
    }
  }


  handleSubmit = async (e) => {
    e.preventDefault();
    if(this.isValidSubmit()) {
      if(this.state.currentAssign === null) {
        console.log("----------- new assign  ------------");
        this.props.addAssign({ // new assign
          title:        this.state.title,
          description:  this.state.description,
          worksheet:    this.state.selectedWorkSheet, 
          assignedTo:   this.props.currentStudent._id
        });
        this.props.handleAssignListChange();
      } else {
        console.log("----------- update assign  ------------");
        this.props.updateAssign({ // new assign
          _id: this.state.currentAssign._id,
          title:        this.state.title,
          description:  this.state.description,
          worksheet:    this.state.selectedWorkSheet, 
        });
      }

    }
  }

  isValidSubmit = () =>{
    return this.state.title !== "" && this.state.selectedWorkSheet !== null
  }

  render() {
    const { worksheets }          = this.props;
    const { title, description, selectedWorkSheet }  = this.state;
    
    const worksheetOptions = worksheets.map((elt) => ({
      label: elt.name,
      value: elt._id
    }))
    
    return (
      <div
        className={`email-app-details ${
          this.props.currentStatus ? "show" : ""
        }`}
      >
        <div className="email-detail-header">
          <div className="email-header-left d-flex align-items-center mb-1">
            <ArrowLeft
              size={20}
              className="mr-1 cursor-pointer"
              onClick={() => {
                this.props.handleAssignDetails("close")
              }}
            />
            {/* <h4 className="mb-0">{currentMail.subject}</h4> */}
          </div>
          <div className="email-header-right mb-1 ml-2 pl-1">
            <ul className="list-inline m-0">
              <li
                className="list-inline-item mr-1 mail-unread"
                onClick={() => {
                  this.props.handleAssignDetails("close")
                }}
              >
                <span className="action-icon">
                  <Home size={22} />
                </span>
              </li>
            </ul>
          </div>
        </div>
        <PerfectScrollbar
          className="email-scroll-area"
          options={{
            wheelPropagation: false
          }}
        >
          <Card className="px-1 mt-1">
            <Row>
              <Col md="12" sm="12" className='mb-1'>
                <h3 className="my-1 text-bold-600 text-center">Select an WorkSheet to Assign</h3>
                <Select
                  className="React"
                  classNamePrefix="select"
                  name="color"
                  options={worksheetOptions}
                  value={worksheetOptions.find((elt) => elt.value === selectedWorkSheet)}
                  onChange={(e, d) => {
                    console.log('e', e);
                    this.setState({
                      selectedWorkSheet: e.value
                    })
                  }}
                />
              </Col>
            </Row>
          </Card>
          <Card className="px-1 mt-1">
            <Row className="mt-1">
              <Col lg="12" md="12" sm="12">
                <FormGroup>
                  <Label for="validState">Title</Label>
                  <Input type="text" id="title"
                    valid={title !== ""}
                    invalid={title === ""}
                    value={title}
                    placeholder="Input Title" 
                    onChange={(e) => {
                      this.setState({
                        title: e.target.value
                      })
                    }}
                  />
                  <FormFeedback 
                    valid={title !== ""}
                    invalid={String(title === "")}
                  >
                    {
                      title !== "" ? "Title Available" : "Input Title"
                    }
                  </FormFeedback>
                </FormGroup>
              </Col>
              <Col lg="12" md="12" sm="12" className="mb-1">
                <div className="form-label-group mt-2 mb-0">
                  <Input
                    type="textarea"
                    name="description"
                    id="description"
                    rows="4"
                    value={description}
                    placeholder="Tell your student more details"
                    onChange={e => this.setState({ description: e.target.value })}
                  />
                  <Label>Description</Label>
                </div>
                <small
                  className={`counter-value float-right ${
                    description.length > 100 ? "bg-danger" : ""
                  }`}
                >
                  {`${description.length}/100`}
                </small>
              </Col>
              <Col lg="12" md="12" sm="12" className="mb-1 text-right">
                <div className="d-inline-block mr-1 mb-1">
                  <Button.Ripple color="primary"
                    onClick={(e) => this.handleSubmit(e)}
                    disabled={!this.isValidSubmit()}
                  >Submit</Button.Ripple>
                </div>
              </Col>
            </Row>
          </Card>
        </PerfectScrollbar>
      </div>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    worksheets: state.worksheet.allData
  }
}

const mapDispatchToProps = {
  getWorkSheets,
  addAssign,
  updateAssign,
}

export default connect(mapStateToProps, mapDispatchToProps)(WorkSheetsList)