import * as Types from "../types";
import { Axios } from "../root"
import { displayInfo, displaySuccessMessage, displayServerErrorMessage } from '../../../service/Alert';

export const getData = (params) => {
  return async (dispatch, getState) => {
    await Axios.get(`/worksheets/management?page=${params.page}&perPage=${params.perPage}`).then(response => {
      dispatch({
        type: Types.FETCH_WORKSHEETS,
        data: response.data.worksheets,
        totalPages: response.data.totalPages,
        params
      })
    })
  }
}

export const getDataById = (id) => {
  return async (dispatch, getState) => {
    await Axios.get(`/worksheets/management/${id}`).then(response => {
      dispatch({
        type: Types.FETCH_WORKSHEET_BY_ID,
        data: response.data.worksheet,
      })
    })
  }
}

export const getInitialData = () => {
  return async (dispatch, getState) => {
    await Axios.get(`/worksheets/management`).then(response => {
      dispatch({ type: Types.FETCH_ALL_WORKSHEETS, data: response.data.worksheets })
    })
  }
}

