export const addData = ({ name, assessments, topicId }) => {
  return (dispatch, getState) => {
    let params = getState().worksheet.params;

    Axios
      .post("/worksheet/create", {
        topicId,
        name,
        assessments
      })
        .then(response => {
          dispatch({ type: Types.ADD_WORKSHEET, obj: response.data.doc })
          dispatch(getData(params));
          displaySuccessMessage('Successfully Created new WorkSheet!')
        })
        .catch(displayServerErrorMessage)
  }
}

export const filterData = value => {
  return dispatch => dispatch({ type: Types.FILTER_WORKSHEET , value })
}

export const updateData = obj => {
  return (dispatch, getState) => {
    let params = getState().worksheet.params;
    Axios
      .put("/worksheet/" + obj._id, {
        name: obj.name,
        topicId: obj.topicId,
        assessments: obj.assessments,
      })
      .then(response => {
        dispatch({ type: Types.UPDATE_WORKSHEET, obj })
        dispatch(getData(params))
        displaySuccessMessage('Successfully updated!')
      })
      .catch(displayServerErrorMessage)
  }
}

export const deleteData = obj => {
  return dispatch => {
    
    Axios
      .delete("/worksheet/" + obj._id)
      .then(response => {
        dispatch({ type: Types.DELETE_WORKSHEET, obj })
        displaySuccessMessage("Successfully deleted " + obj.name)
      })
      .catch(err => {
        displayServerErrorMessage(err)
      })
  }
}

export const deleteDataByIds = ids => {
  return dispatch => {
    
    Axios
      .put("/worksheet-delete", {
        ids
      })
      .then(response => {

        const deletedIds  = response.data.deletedIds
        const relatedData = response.data.relatedData

        if(deletedIds.length > 0) {
          displaySuccessMessage(`Successfully deleted ${deletedIds.length} data!`)
        }
        if(relatedData.length > 0) {
          displayInfo(`${relatedData.length} data ${relatedData.length > 1 ? "have" : "has"} been assigned to users.`);
        }

        dispatch({ type: Types.DELETE_WORKSHEETS, ids: deletedIds })
      })
      .catch(err => {
        displayServerErrorMessage(err)
      })
  }
}