import * as Types from "../types";
import { Axios } from "../root"
import { displayServerErrorMessage, displayInfo, displaySuccessMessage } from '../../../service/Alert';


export const getData = (params) => {
  return async dispatch => {
    var url = "";
    if(params && params.page) {
      if(isNaN(params.page)) {
        params.page = 1
      }
      if(isNaN(params.perPage)) {
        params.perPage = 4
      }
      url = `/levels?page=${params.page}&perPage=${params.perPage}`;
    } else {
      url = `/levels?page=1&perPage=4`;
    }
    await Axios.get(url)
      .then(response => {
        dispatch({
          type: Types.FETCH_LEVELS,
          data: response.data.levels,
          totalPages: response.data.totalPages,
          params
        })
      })
      .catch(displayServerErrorMessage)
  }
}

export const addData = (obj) => {
  return (dispatch, getState) => {
    let params = getState().level.params;

    const formData = new FormData();
    formData.append('image', obj.file);
    formData.append('name', obj.name)
    formData.append('description', obj.description)

    Axios
      .post("/level/create", formData)
      .then(response => {
        dispatch({ type: Types.ADD_LEVEL, obj: response.data.doc })
        dispatch(getData(params))
        displaySuccessMessage("Successfully Submitted")
      })
      .catch(displayServerErrorMessage)
  }
}

export const getInitialData = () => {
  return async dispatch => {
    await Axios.get("/levels").then(response => {
      dispatch({ type: Types.FETCH_ALL_LEVEL, data: response.data.levels })
    })
  }
}

export const filterData = value => {
  return dispatch => dispatch({ type: Types.FILTER_LEVEL , value })
}

export const deleteData = obj => {
  return dispatch => {
    
    Axios
      .delete("/level/" + obj._id)
      .then(response => {
        dispatch({ type: Types.DELETE_LEVEL, obj });
        displaySuccessMessage("Successfully  deleted!");
      })
      .catch(displayServerErrorMessage)
  }
}
export const deleteDatas = ids => {
  return dispatch => {
    
    Axios
      .put("/levels-delete", {ids: ids})
      .then(response => {
        if(response.data.relatedData && response.data.relatedData.length > 0) {
          displayInfo(`${response.data.relatedData.length} data has relevant data.`);
        }
        if(response.data.deletedIds.length > 0) {
          displaySuccessMessage("Successfully deleted");
        } 
        dispatch({ type: Types.DELETE_LEVELS, ids: response.data.deletedIds });

      })
      .catch(displayServerErrorMessage)
  }
}

export const updateData = obj => {
  return (dispatch, getState) => {
    const formData = new FormData();

    formData.append('image', obj.file);
    formData.append('name', obj.name)
    formData.append('description', obj.description);
    let params = getState().level.params;
    Axios
      .put("/level/" + obj._id, formData)
      .then(response => {
        dispatch({ type: Types.UPDATE_LEVEL, obj })
        dispatch(getData(params))
      })
      .catch(displayServerErrorMessage)
  }
}