import React from "react"
import {
  FormGroup,
  Input,
  Button
} from "reactstrap"
import {
  Search,
  Menu,
  PlusSquare,
} from "react-feather"
import PerfectScrollbar from "react-perfect-scrollbar"
import { connect } from "react-redux"
import WorkSheetsList from "./WorkSheetsList"
import {
  getAssignedListByFilter,
  deleteData as deleteAssign
} from "../../../../redux/actions/assigned"
import MarkingComponent from "./MarkingComponent";
import AssignItem from "./Components/AssignItem";
import ScoreItem from "./Components/ScoreItem";

class AssignedList extends React.Component {
  static getDerivedStateFromProps(props, state) {
    // Return null if the state hasn't changed
    return null
  }
  state = {
    emailDetailsVisibility: false,
    selectAll: false,
    currentAssign: null,
    value: "",
  }

  async componentDidUpdate(prevProps, prevState) {
    if(!Object.is(this.props.currentStudent, prevProps.currentStudent)) {
      this.setState({
        currentAssign: null
      })
      if(this.props.currentStudent && this.props.currentStudent._id) {
        this.getListOfAssignByStudentId(this.props.currentStudent._id);
      }
    }
  }

  getListOfAssignByStudentId = async (id) => {
    await this.props.getAssignedListByFilter({
      assignedTo: this.props.currentStudent._id
    });
  }

  handleAssignDetails = (status, assignedTask) => {
    if (status === "open")
      this.setState({ emailDetailsVisibility: true, currentAssign: assignedTask })
    else this.setState({ emailDetailsVisibility: false, currentAssign: null })
  }

  handleAssignListChange = () => {
    if(this.props.currentStudent && this.props.currentStudent._id) {
      this.getListOfAssignByStudentId(this.props.currentStudent._id);
    }
  }

  handleOnChange = e => {
    this.setState({ value: e.target.value })
  }

  handleDeleteAssign = (item) => {
    this.props.deleteAssign(item);
  }

  render() {
    const { value, currentAssign } = this.state;
    const { filteredList, pageType } = this.props;
    
    let RenderComponent;
    let renderfilteredList = <div className="no-results show">
      <h5>No Task Found</h5>
    </div>

    switch (pageType) {
      case 'mark':
        RenderComponent = MarkingComponent;
        break;
      case 'assign':
        RenderComponent = WorkSheetsList;
        break;
    
      default:
        RenderComponent = WorkSheetsList
        break;
    }
    if(filteredList.length > 0 && (pageType === 'assign')) {
      renderfilteredList = filteredList.map((assignedTask) => {
        return (
          <AssignItem
            key={assignedTask._id}
            assignedTask={assignedTask}
            handleAssignDetails={this.handleAssignDetails}
            handleDeleteAssign={this.handleDeleteAssign}
          />
        )
      })
    } else if(filteredList.length > 0 && pageType === 'score') {
      renderfilteredList = filteredList.map((assignedTask) => {
        if (this.props.filterOption.selectedTopicId !== "" && assignedTask.worksheet.topicId === this.props.filterOption.selectedTopicId) {
          return (
            <ScoreItem
              key={assignedTask._id}
              assign={assignedTask}
              handleAssignDetails={this.handleAssignDetails}
            />
          )
        }
        else if (!this.props.filterOption.selectedTopicId) {
          return (
            <ScoreItem
              key={assignedTask._id}
              assign={assignedTask}
              handleAssignDetails={this.handleAssignDetails}
            />
          )
        }
      })
    }
    else if (filteredList.length > 0 && pageType === 'mark') {
      renderfilteredList = filteredList.map((assignedTask) => {
        if (this.props.filterOption.selectedTopicId !== "" && assignedTask.worksheet.topicId === this.props.filterOption.selectedTopicId) {
          return (
            <AssignItem
              key={assignedTask._id}
              assignedTask={assignedTask}
              handleAssignDetails={this.handleAssignDetails}
              handleDeleteAssign={this.handleDeleteAssign}
            />
          )
        }
        else if (!this.props.filterOption.selectedTopicId) {
          return (
            <AssignItem
              key={assignedTask._id}
              assignedTask={assignedTask}
              handleAssignDetails={this.handleAssignDetails}
              handleDeleteAssign={this.handleDeleteAssign}
            />
          )
        }
      })
    }

    return (
      <div className="content-right h-100">
        <div className="email-app-area">
          <div className="email-app-list-wrapper">
            <div className="email-app-list">
              <div className="app-fixed-search pt-1 pb-1">
                <div
                  className="d-lg-none sidebar-toggle"
                  onClick={() => this.props.mainSidebar(true)}
                >
                  <Menu size={24} />
                </div>
                <FormGroup className="position-relative has-icon-left m-0 d-inline-block  w-100 d-flex">
                  <div className='' style={{width: 'calc(100% - 52px)'}}>
                    <Input
                      placeholder="Search..." 
                      onChange={e => {
                        this.handleOnChange(e)
                      }}
                      value={value}
                    />
                    <div className="form-control-position">
                      <Search size={15} />
                    </div>
                  </div>
                  {
                    this.props.currentStudent && <div className="d-inline-block float-right">
                      {" "}
                      <Button.Ripple className="btn-icon" color="success primary"
                        onClick={() => {
                          this.handleAssignDetails("open", null)
                        }}
                      >
                        <PlusSquare size='16' />
                      </Button.Ripple>
                    </div>
                  }
                </FormGroup>
              </div>
              <PerfectScrollbar
                className="email-user-list list-group"
                options={{
                  wheelPropagation: false
                }}
              >
                {
                  this.props.currentStudent
                    ? <ul className="users-list-wrapper media-list">{renderfilteredList}</ul>
                    : 
                    <div className="p-3 my-2 rounded text-center">
                      <h2>Select A Student</h2>
                    </div>
                }
              </PerfectScrollbar>
            </div>
          </div>
          <RenderComponent
            handleAssignDetails={this.handleAssignDetails}
            currentStatus={this.state.emailDetailsVisibility}
            currentStudent={this.props.currentStudent}
            currentAssign={currentAssign}
            handleAssignListChange={this.handleAssignListChange}
            filteredList={filteredList}
          />  
        </div>
      </div>
    )
  }
}
const mapStateToProps = state => {
  return {
    starred: state.emailApp.starred,
    filteredList: state.assigned.filteredList
  }
}
export default connect(mapStateToProps, {
  getAssignedListByFilter,
  deleteAssign
})(AssignedList)
