import { history } from "../../../history";
import { subRooter } from "../../../configs/vars";
import * as Types from "../types";
import { Axios } from "../root"
import { displayServerErrorMessage, displaySuccessMessage } from '../../../service/Alert';

export const getData = () => {
  return (dispatch, getState) => {
    return dispatch()
  }
}

export const getParentData = () => {
  return dispatch => {
    Axios.get('/category/parents')
      .then(response => {
        dispatch({ type: Types.FETCH_ALL_CATEGORY_PARENT, data: response.data.parents })
      })
  }
}

export const addData = (obj) => {
  return (dispatch, getState) => {
    const formData = new FormData();
    formData.append('image', obj.file);
    formData.append('name', obj.name)
    formData.append('parentId', obj.parentId)

    Axios
      .post("/category/create", formData)
      .then(response => {
        displaySuccessMessage("Successfully Created")
        dispatch({ type: Types.ADD_CATEGORY, data: response.data.doc})
        history.push(`${subRooter}/categories`);
        dispatch(getParentData())
      })
      .catch((err) => {
        displayServerErrorMessage(err);
      })
  }
}

export const updateData = (obj) => {
  return (dispatch, getState) => {
    const formData = new FormData();
    formData.append('image', obj.file);
    formData.append('name', obj.name)
    formData.append('parentId', obj.parentId)

    Axios
      .put("/category/" + obj._id, formData)
      .then(response => {
        displaySuccessMessage("Successfully Updated")
        dispatch({ type: Types.UPDATE_CATEGORY })
        dispatch(getParentData())
      })
      .catch((err) => {
        displayServerErrorMessage(err);
      })
  }
}

export const deleteData = (obj) => {
  return dispatch => {
    Axios
      .delete("/category/" + obj._id)
      .then(response => {
        displaySuccessMessage("Successfully Deleted")
        dispatch({ type: Types.DELETE_CATEGORY, data: obj})
        dispatch(getParentData())
      })
      .catch((err) => {
        displayServerErrorMessage(err);
      })
  }
}